.App {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  text-align: center;
}

.Main {
  padding: 100px 0;
}
.Main__button-generate {
  background-color: transparent;
  border-radius: 10px;
  border:1px solid #666;
  color: #666;
  -webkit-transition: background 1s ease;
  transition: background 1s ease;
  display: flex;
  align-items: center;
  margin: auto;
  padding: 0 10px;
}
.Main__button-generate:hover {
  color: #fff;
  cursor: pointer;
  background-color: #000;
  cursor: pointer;
}

.title {
  font-family: 'Anton', sans-serif;
  letter-spacing: 2px;
}

.quote {
  display: flex;
  flex-direction: column;
  padding: 20px;
  background-color: antiquewhite;
  margin: 10px auto;
  width: 80%;
  border-radius: 15px;
}

.quote--favorite {
  margin: 20px;
  background-color: antiquewhite;
}

.quote__cite {
  font-family: 'Handlee', sans-serif;
}

.quote__symbol {
  font-family: Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;
  font-size: 3.5rem;
  margin-right: 25px;
}

.quote__author {
  font-size: 0.7em;
  letter-spacing: 2px;
  font-family: 'Roboto Mono', sans-serif;
  background-color: black;
  color: #fff;
  width: 100%;
  margin: 15px auto;
  border-radius: 20px;
  padding: 5px 0;
}

.hero img {
  width: 100%;
}


@media (min-width: 401px) {
  .Main {
    margin-top: 20px;
  }

  .quote__author {
    width: 50%;
  }

  .hero img {
    width: 50%;
  }
}

@media (min-width: 901px) {
  .Main {
    padding: 100px 50px;
    border-radius: 50%;
    width: 50%;
    margin: auto;
  }
}

